<template>
  <view class="tab-bar">
    <view v-for="(item, index) in tabs" :key="index" class="tab-item" @click="switchTab(index)">
      <image :src="currentIndex === index ? item.selectedIcon : item.icon"></image>
      <text :class="{active: currentIndex === index}">{{ item.title }}</text>
    </view>
	<router-view></router-view>
  </view>
</template>

<script>
export default {
	name: 'TabBar',
  props: {
    tabs: {
      type: Array,
      default() {
        return []
      }
    }
  },
  data() {
    return {
      currentIndex: 0
    }
  },
  methods: {
    switchTab(index) {
      this.currentIndex = index
      uni.switchTab({
        url: this.tabs[index].path
      })
    }
  }
}
</script>

<style scoped>
.tab-bar {
  display: flex;
  justify-content: space-around;
  align-items: center;
  height: 60px;
  box-shadow: 0 -1px 5px rgba(0, 0, 0, .1);
  background-color: #ffffff;
}

.tab-item {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.tab-item image {
  height: 20px;
  width: 20px;
}

.tab-item .active {
  color: #007aff;
}
</style>
